import React, { useState, useEffect } from "react";
import { Button, PageHeader, Space, Table ,Modal} from "antd";
import { harcamaService } from "../services/harcama.services";
import { PlusSquareOutlined } from "@ant-design/icons";
import ServerEdit from "./ServerEdit";

const Serverlist = (props) => {
  const [state, setState] = useState({ dataList: [] });
  const [isModalVisible, setIsModalVisible] = useState(false);

  useEffect(() => {
    harcamaService.serverList().then((data) => {
        setState({ 
            dataList: data,
            selectedRowKeys: [],
         });
      });
  }, []);

  const columns = [
    {
      title: 'ID',
      dataIndex: 'objectId',
      key: 'objectId',
      
    },
    {
      title: 'IP',
      dataIndex: 'ip',
      key: 'ip',
    },
    {
      title: 'kullanici',
      dataIndex: 'kullanici',
      key: 'kullanici',
     
    },
    {
        title: 'Password',
        dataIndex: 'password',
        key: 'password',
      }
      ,
    {
        title: 'Açıklama',
        dataIndex: 'aciklama',
        key: 'aciklama',
      }
  ];

  const onSelectChange = selectedRowKeys => {
    console.log('selectedRowKeys changed: ', selectedRowKeys);
    setState({ ...state,selectedRowKeys });
  };

  const rowSelection = {
    selectedRowKeys:state.selectedRowKeys,
    onChange:onSelectChange,
  };

  const summary= (pageData)=>{
    let sum=0;
    if(pageData.length>0){
      sum=pageData.map(item => item.miktar).reduce((prev, next) => prev + next);
    }

    return (
      <Table.Summary fixed>
        <Table.Summary.Row>
          <Table.Summary.Cell index={0}></Table.Summary.Cell>
          <Table.Summary.Cell index={1}></Table.Summary.Cell>
          <Table.Summary.Cell index={2}></Table.Summary.Cell>
          <Table.Summary.Cell index={3}>Miktar :{sum}</Table.Summary.Cell>
          <Table.Summary.Cell index={4}></Table.Summary.Cell>
        </Table.Summary.Row>
      </Table.Summary>
    )

  }

  const showModal = () => {
    setIsModalVisible(true);
  };

  const handleOk = () => {
    setIsModalVisible(false);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };


  const newRecord = () =>{
    showModal();
  }


  return (
    <>
    <Modal title="" okButtonProps={{hidden:true}} cancelButtonProps={{hidden:true}} visible={isModalVisible} onOk={handleOk} onCancel={handleCancel}>
      <br/>
      <p>
        <ServerEdit handleOk={handleOk}/>
      </p>
    </Modal>
      <Space>
        <Button onClick={newRecord} type="primary" icon={<PlusSquareOutlined />}>Yeni Kayıt</Button>
        <PageHeader title="Server Liste Ekranı"/>
      </Space>
      <Table rowKey="objectId" rowSelection={rowSelection} dataSource={state.dataList} columns={columns}  summary={summary} />;
      
    </>
  );
};

export default Serverlist;



