import React from "react";
import { Button, Col, Form, Input, message, Row } from "antd";
import { harcamaService } from "../services/harcama.services";

const ServerEdit = (props) => {


  const saveOnClick=(values)=>{
    console.log(values);
    harcamaService.serverSave(values).then(result=>{
      message.success('İşlem Başarılı');
    });
    props.handleOk();

  }

  return (
    <Form labelCol={{ span: 8 }} wrapperCol={{ span: 16 }} onFinish={saveOnClick}>
      <Row>
        <Col span={24}>
          <Form.Item name="ip" label="İp Adresi">
            <Input />
          </Form.Item>
          <Form.Item name="kullanici" label="Kullanıc Adı">
            <Input />
          </Form.Item>
        </Col>
        <Col span={24}>
          <Form.Item name="password" label="Şifre">
            <Input.Password />
          </Form.Item>
          <Form.Item name="aciklama" label="Açıklama">
            <Input />
          </Form.Item>
        </Col>
      </Row>
      <Form.Item wrapperCol={{ offset: 18, span: 6 }}>
        <Button type="primary" htmlType="submit">
          Kaydet
        </Button>
      </Form.Item>
    </Form>
  );
};

export default ServerEdit;
