import { callApi } from "./base.services";

async function serverList() {
  var serviceName = "harcamalar/serverlist";
  try {
    let response = await callApi(serviceName, {});
    return response;
  } catch (err) {
    return Promise.reject(err);
  }
}

async function taksitList() {
    var serviceName = "harcamalar/TaksitList";
    try {
      let response = await callApi(serviceName, {});
      return response;
    } catch (err) {
      return Promise.reject(err);
    }
  }

async function serverSave(data) {
  var serviceName = "Crud/ServerAdd";
  const dataObj = JSON.stringify(data);
  try {
    let response = await callApi(serviceName, {
      method: "POST",
      data: dataObj,
    });
    return response;
  } catch (err) {
    return Promise.reject(err);
  }
}

async function CyclomediaList() {
  var serviceName = "harcamalar/Cyclomedialist";
  try {
    let response = await callApi(serviceName, {});
    return response;
  } catch (err) {
    return Promise.reject(err);
  }
}

export const harcamaService = { serverList,serverSave, CyclomediaList, taksitList };
